  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 1001;
  /* Выше оверлея */
  transform: translateY(-50px);
  /* Начальное положение сверху */
  transition: transform 0.3s ease, opacity 0.3s ease;
  /* Переход для положения и прозрачности */ }
  .modal.visible {
    transform: translateY(0);
    /* Положение при видимости */
    opacity: 1;
    /* Прозрачность при видимости */ }

.modal-content {
  padding: 30px;
